const mixin = {
    data() {
        return {
            data,
            columns,
            pagination: {
                showQuickJumper: true,
                defaultPageSize: 1,
                showTotal: (total) => `共 ${total} 条数据`,
                showSizeChanger: true,
                defaultCurrent: 1,
                pageSizeOptions: ["1", "2", "15", "20"],
                onShowSizeChange: (current, pageSize) => (this.pageSize = pageSize),
            },
        }

    },
    methods: {

    },
}
export default mixin
